import { newSpecPage } from '@stencil/core/testing';
import { PrestoEmailOverlayController } from '../presto-email-overlay-controller';

describe('presto-cta-overlay-controller', () => {
  it('renders without overlay by default', async () => {
    const page = await newSpecPage({
      components: [PrestoEmailOverlayController],
      html: `<presto-email-overlay-controller></presto-email-overlay-controller>`,
    });
    expect(page.root).toEqualHtml(`
      <presto-email-overlay-controller></presto-email-overlay-controller>
    `);
  });
});
